.category-heading {
  color: #444;
  border-bottom: 2px solid #e9ecef;
  margin-top: 40px;
  margin-bottom: 20px;
  padding-bottom: 5px;
  font-size: 1.6em;
  font-weight: 500;
}

#projects-content {
  box-sizing: border-box;
  background-color: #fff;
  width: 100%;
  max-width: 900px;
  margin-top: 6px;
  margin-left: auto;
  margin-right: auto;
  padding: .5rem 1.5rem 1rem;
  overflow-x: hidden;
  box-shadow: 0 0 10px #00000014;
}

.content.hero-page .hero {
  height: calc(48vh - var(--site-header-height));
  padding-top: 6px;
}

@media (width <= 600px) {
  .content.hero-page .hero {
    height: calc(30vh - var(--site-header-height));
    padding-top: 4px;
  }

  #projects-content {
    margin-top: 4px;
    padding-top: .5rem;
  }
}

.project-item {
  border: 1px solid var(--muted-border);
  background-color: var(--card-bg);
  box-shadow: var(--shadow);
  border-radius: 8px;
  margin-bottom: 25px;
  padding: 20px;
  transition: transform .2s, box-shadow .2s;
}

.project-title {
  color: var(--accent);
  margin-bottom: 10px;
  font-size: 1.5em;
  font-weight: 600;
  line-height: 1.2;
}

.project-actions {
  margin-bottom: 15px;
}

.action-link {
  color: #495057;
  border: 1px solid #ced4da;
  border-radius: 4px;
  align-items: center;
  margin-right: 15px;
  padding: 5px 10px;
  font-size: .9em;
  font-weight: 600;
  text-decoration: none;
  transition: background-color .2s, color .2s, border-color .2s;
  display: inline-flex;
}

.action-link:hover {
  color: #0056b3;
  background-color: #e9ecef;
  border-color: #0056b3;
}

.action-link i {
  margin-right: 6px;
}

.project-description {
  color: #555;
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.05em;
  line-height: 1.4;
}

.project-details {
  border-top: 1px dashed #e9ecef;
  margin-top: 10px;
  padding-top: 10px;
}

.detail-tag {
  text-transform: uppercase;
  border-radius: 4px;
  margin-right: 12px;
  padding: 4px 10px;
  font-size: .85em;
  font-weight: 500;
  display: inline-block;
}

.language-tag {
  color: #007bff;
  background-color: #e6f7ff;
}

.library-tag {
  color: #28a745;
  background-color: #e6ffe6;
}

.paper-ref {
  color: #fd7e14;
  background-color: #fff3e6;
}

.wip-note {
  color: #e49a00;
  vertical-align: middle;
  background-color: #fffbe6;
  border-radius: 4px;
  margin-left: 8px;
  padding: 3px 6px;
  font-size: .8em;
  font-style: normal;
  font-weight: 600;
}

.project-references {
  background-color: var(--card-bg);
  border-left: 5px solid var(--accent);
  border-radius: 8px;
  margin-top: 40px;
  padding: 20px;
}

.project-references h4 {
  color: #1a1a1a;
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 1.2em;
  font-weight: 600;
}

.project-references p {
  color: #555;
  margin-bottom: 5px;
  font-size: .95em;
}

.project-group {
  background-color: #fff;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  margin-bottom: 25px;
  padding: 20px;
  box-shadow: 0 2px 4px #0000000d;
}

.project-group .project-item {
  box-shadow: none;
  background-color: #0000;
  border: none;
  margin-bottom: 20px;
  padding: 0;
  transition: none;
}

.project-group .project-item:first-child {
  border-bottom: 1px dashed #ced4da;
  margin-bottom: 20px;
  padding-bottom: 20px;
}

.project-group .project-item:hover {
  box-shadow: none;
  transform: none;
}

.project-references.grouped-reference {
  box-shadow: none;
  background-color: #f8f9fa;
  border-left: 4px solid #0056b3;
  border-radius: 6px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 15px;
}

.project-item {
  background-color: #fff;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  margin-bottom: 25px;
  padding: 20px;
  transition: transform .2s, box-shadow .2s;
  box-shadow: 0 2px 4px #0000000d;
}

.project-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 4px 8px #0000001a;
}
